<script>export let components;
export let a11yColors;
export let hex;
export let color;
export let a11yGuidelines;
export let isA11yOpen;
export let isA11yClosable;
$: closable = isA11yOpen && !isA11yClosable;
</script>

<details class="a11y-notice {closable ? 'not-closable' : ''}" open={isA11yOpen}>
	<summary tabindex={closable ? -1 : undefined}>
		<svelte:component this={components.a11ySummary} {a11yColors} {hex} />
	</summary>
	<div>
		{#each a11yColors as { hex: a11yHex, placeholder, reverse, size }}
			<svelte:component
				this={components.a11ySingleNotice}
				contrast={color?.contrast(a11yHex)}
				textColor={reverse ? a11yHex : hex}
				bgColor={reverse ? hex : a11yHex}
				ph={placeholder}
				{size}
			/>
		{/each}
		{#if a11yGuidelines}
			<span>
				{@html a11yGuidelines}
			</span>
		{/if}
	</div>
</details>

<style>
	details {
		width: 230px;
		margin: 8px auto;
	}

	summary {
		margin-bottom: 8px;
		cursor: pointer;
		transition: color 0.2s;
	}

	summary:hover {
		color: blue;
	}

	.not-closable summary {
		pointer-events: none;
	}

	.not-closable summary {
		list-style: none;
	}

	:focus-visible,
	span :global(:focus-visible) {
		border-radius: 2px;
		outline: 2px solid var(--focus-color, red);
		outline-offset: 2px;
	}
</style>
